import '../hojas-de-estilo/Contact.css';
import { BsEnvelopeFill, BsLinkedin, BsGithub, BsInstagram } from 'react-icons/bs'
import 'animate.css';
import TrackVisibility from 'react-on-screen';


export const Contact = () => {
  return (
    <section className="contact-section" id="contact">

      <TrackVisibility>
      {({ isVisible }) => 
      <div className={isVisible ? 'animate__animated animate__fadeInDown' : ''}>
      <h2 className='contact-title'>Contact</h2>
      </div>}
      </TrackVisibility>



      <div className='sm-container'>
        <div className='sm'>
          <TrackVisibility>
          {({ isVisible }) => 
            <div className={isVisible ? 'animate__animated animate__fadeInUp' : ''}>
              <a className='btn-sm' href='mailto:chaveznicolas3701@gmail.com' target='_blank' rel='noreferrer'><BsEnvelopeFill size={'2.5rem'}/></a>
            </div>}
          </TrackVisibility>
        </div>
        <div className='sm'>
          <TrackVisibility>
          {({ isVisible }) => 
            <div className={isVisible ? 'animate__animated animate__fadeInUp' : ''}>          
              <a className='btn-sm' href='https://www.linkedin.com/in/nicolas-chavez-b69493234/' target='_blank' rel='noreferrer'><BsLinkedin size={'2.5rem'}/></a>
            </div>}
          </TrackVisibility>
        </div>
        <div className='sm'>
          <TrackVisibility>
          {({ isVisible }) => 
            <div className={isVisible ? 'animate__animated animate__fadeInUp' : ''}>    
              <a className='btn-sm' href='https://github.com/nicochgz' target='_blank' rel='noreferrer'><BsGithub size={'2.5rem'}/></a>
            </div>}
          </TrackVisibility>
        </div>
        <div className='sm'>
          <TrackVisibility>
          {({ isVisible }) => 
            <div className={isVisible ? 'animate__animated animate__fadeInUp' : ''}>  
              <a className='btn-sm' href='https://www.instagram.com/nicochgz' target='_blank' rel='noreferrer'><BsInstagram size={'2.5rem'}/></a>
            </div>}
          </TrackVisibility>         
        </div>
      </div>
    </section>
  )
}