const projectImages = require.context('../assets/img', true);

export const Projects = () => { 
  const [tag, setTag] = useState('all');
  const [filtered, setFiltered] = useState([]);

  useEffect( () => {
    tag === 'all' ? setFiltered(projects) : setFiltered(projects.filter( project => project.tag === tag))
  }, [tag])

  return (
    <section className="projects-section" id="projects">
      <div className='projects-container'>
        <h2>PROJECTS</h2>
        <div className='tag-buttons'>
          <TagButton name='all' handleSetTag={setTag} tagActive={ tag === 'all' ? true : false}/>
          <TagButton name='web' handleSetTag={setTag} tagActive={ tag === 'web' ? true : false}/>
          <TagButton name='modeling' handleSetTag={setTag} tagActive={ tag === 'modeling' ? true : false}/>
          <TagButton name='others' handleSetTag={setTag} tagActive={ tag === 'others' ? true : false}/>

        </div>

        <div className='container-list'>
          {filtered.map(project => 
        <TrackVisibility>
      {({ isVisible }) => 

            <div key={project.id} className={isVisible ? 'animate__animated animate__headShake ' : ''}>
              <div className='project-card'>
                <div className='img-desc-container'>
                  <div className='desc-container'>
                    <p className='project-desc'>{project.description}</p>
                  </div>
                  <img className='project-image' src={ projectImages(`./${project.imageName}.jpg`) } alt=''/>
                </div>
                <div className='tit-btn-container'>
                  <h2>{project.projectName}</h2>
                  <a href={project.projectLink} target='_blank' rel='noreferrer'>Project</a>
                </div>
              </div>
            </div>}
            </TrackVisibility>
            )


          }
        </div>


      </div>
    </section>
  )
}

const TagButton = ({name, handleSetTag, tagActive}) => {
  return <button className={`tag ${ tagActive
   ? 'active-project' : null }`} onClick={() =>  handleSetTag(name)}>{name.toUpperCase()}</button>
}

