import React from 'react';
import 'animate.css';
import TrackVisibility from 'react-on-screen';
import '../hojas-de-estilo/Skills.css';
const TagCloud = require('TagCloud');
 

const logoImages = require.context('../assets/img', true);

export const Skills = () => {

  React.useEffect(() => {
    const container = '.sphere';
    const texts = [
      'JavaScript', 'PHP', 'HTML',
      'CSS3', 'BOOTSTRAP', 'ReactJs',
      'VueJs', 'NodeJs', 'ExpressJs',
      'MongoDB', 'MySql', 'Laravel',
      'Postman', 'NPM', 'Maya', 'Unity'
    ];
    const options = {
      radius: 450,

      maxSpeed: 'fast',
      initSpeed: 'fast',

      direction: 135,

      keep: true
    };
    TagCloud(container, texts, options);
  }, [])

    return (
      <section className="skills-section" id="skills">
        <div className='skills-container'>
        <TrackVisibility>
        {({ isVisible }) => 
          <div className={isVisible ? 'animate__animated animate__fadeIn skills-title' : 'skills-title'}>
            <h2>Skills and Knowledges</h2>
          </div>}
        </TrackVisibility>
          <div className='skills-subcontainer'>
            <div className='skills-web'>             
              <div className='front-container'>

                <div className='front-title'>
                  <TrackVisibility>
                  {({ isVisible }) => 
                  <div className={isVisible ? 'animate__animated animate__fadeIn' : ''}>
                    <h3>Front-End</h3>
                  </div>}
                  </TrackVisibility>
                </div>


                <TrackVisibility>
                {({ isVisible }) => 
                  <div className={isVisible ? 'animate__animated animate__zoomIn front-list' : 'front-list'}>
                    <div className='front-item'>
                      <img src={ logoImages(`./logo-html5.svg`) } alt=''></img>
                      <span>HTML5</span>
                    </div>
                    <div className='front-item'>
                      <img src={ logoImages(`./logo-css3.svg`) } alt=''></img>
                      <span>CSS3</span>
                    </div>
                    <div className='front-item'>
                      <img src={ logoImages(`./logo-js.svg`) } alt=''></img>
                      <span>JavaScript</span>
                    </div>
                    <div className='front-item'>
                      <img src={ logoImages(`./logo-bootstrap.svg`) } alt=''></img>
                      <span>Bootstrap</span>
                    </div>
                    <div className='front-item'>
                      <img src={ logoImages(`./logo-reactjs.svg`) } alt=''></img>
                      <span>ReactJs</span>
                    </div>
                    <div className='front-item'>
                      <img src={ logoImages(`./logo-vue.svg`) } alt=''></img>
                      <span>VueJs</span>
                    </div> 
                  </div>}
                </TrackVisibility>

              </div>
              
              <div className='back-container'>
                <div className='back-title'>

                <TrackVisibility>
                  {({ isVisible }) => 
                  <div className={isVisible ? 'animate__animated animate__fadeIn' : ''}>
                    <h3>Back-End</h3>
                  </div>}
                  </TrackVisibility>
                </div>

                <TrackVisibility>
                  {({ isVisible }) => 
                  <div className={isVisible ? 'animate__animated animate__zoomIn back-list' : 'back-list'}>
                    <div className='back-item'>
                      <img src={ logoImages(`./logo-nodejs.svg`) } alt=''></img>
                      <span>NodeJs</span>
                    </div>
                    <div className='back-item'>
                      <img src={ logoImages(`./logo-express.svg`) } alt=''></img>
                      <span>ExpressJs</span>
                    </div>
                    <div className='back-item'>
                      <img src={ logoImages(`./logo-mongo.svg`) } alt=''></img>
                      <span>MongoDB</span>
                    </div>
                    <div className='back-item'>
                      <img src={ logoImages(`./logo-mysql.svg`) } alt=''></img>
                      <span>MySQL</span>
                    </div>
                    <div className='back-item'>
                      <img src={ logoImages(`./logo-php.svg`) } alt=''></img>
                      <span>PHP</span>
                    </div>
                    <div className='back-item'>
                      <img src={ logoImages(`./logo-laravel.svg`) } alt=''></img>
                      <span>Laravel</span>
                    </div>
                  </div>}
                </TrackVisibility>



              </div>
            </div>
            <div className='skills-variado'>
              <div className='variado-container'>
                <div className='variado-title'>

                <TrackVisibility>
                  {({ isVisible }) => 
                  <div className={isVisible ? 'animate__animated animate__fadeIn' : ''}>
                    <h3>Tools and Softwares</h3>
                  </div>}
                  </TrackVisibility>
                  
                </div>

                <TrackVisibility>
                {({ isVisible }) => 
                  <div className={isVisible ? 'animate__animated animate__zoomIn variado-list' : 'variado-list'}>
                    <div className='variado-item'>
                      <img src={ logoImages(`./logo-vscode.svg`) } alt=''></img>
                      <span>VsCode</span>
                    </div>
                    <div className='variado-item'>
                      <img src={ logoImages(`./logo-postman.svg`) } alt=''></img>
                      <span>Postman</span>
                    </div>
                    <div className='variado-item'>
                      <img src={ logoImages(`./logo-npm.svg`) } alt=''></img>
                      <span>NPM</span>
                    </div>
                    <div className='variado-item'>
                      <img src={ logoImages(`./logo-unity.svg`) } alt=''></img>
                      <span>Unity</span>
                    </div>
                    <div className='variado-item'>
                      <img src={ logoImages(`./logo-maya.svg`) } alt=''></img>
                      <span>Maya</span>
                    </div>
                    <div className='variado-item'>
                      <img src={ logoImages(`./logo-photoshop.svg`) } alt=''></img>
                      <span>Photoshop</span>
                    </div>
                    <div className='variado-item'>
                      <img src={ logoImages(`./logo-illustrator.svg`) } alt=''></img>
                      <span>Illustrator</span>
                    </div>
                    <div className='variado-item'>
                      <img src={ logoImages(`./logo-premiere.svg`) } alt=''></img>
                      <span>Premiere Pro</span>
                    </div>
                    <div className='variado-item'>
                      <img src={ logoImages(`./logo-audition.svg`) } alt=''></img>
                      <span>Audition</span>
                    </div>
                    <div className='variado-item'>
                      <img src={ logoImages(`./logo-aftereffects.svg`) } alt=''></img>
                      <span>After Effects</span>
                    </div>
                  </div>}
                </TrackVisibility>



              </div>
            </div>
          </div>
        </div>

        <span className='sphere'></span>

      </section>
    )
}