import '../hojas-de-estilo/Loader.css';
import 'animate.css';
import TrackVisibility from 'react-on-screen';

export const Loader = () => {

  return (
    <div>

        <section className="loader-container">
      <TrackVisibility>
      {({ isVisible }) => 
      <div className={isVisible ? 'animate__animated animate__zoomIn' : ''}>
          <div className='loader'>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div>
      </div>}
      </TrackVisibility>
        </section>

    </div>
  ) 
}