import '../hojas-de-estilo/Menu.css';
import { useState } from 'react';
import { BsHouseDoorFill, BsFillPersonFill, BsFillTerminalFill, BsFillBriefcaseFill, BsFillChatSquareTextFill, BsFillGearFill } from 'react-icons/bs'
export const Menu = () => {
  const [activeLink, setActiveLink] = useState();
  
  const onUpdateActiveLink = (value) => {
    setActiveLink(value);
  }

  return (
    <nav className="menu">
      <div className="menu-options">
        <a href="#home" className={activeLink === 'home' ? 'active menu-link' : 'menu-link'} onClick={() => onUpdateActiveLink('home')}><BsHouseDoorFill size='1.3em'/></a>
        <span className="span-menu">Home</span>
      </div>
      <div className="menu-options">
        <a href="#about" className={activeLink === 'about' ? 'active menu-link' : 'menu-link'} onClick={() => onUpdateActiveLink('about')}><BsFillPersonFill size='1.3em'/></a>
        <span className="span-menu">About Me</span>
      </div>
      <div className="menu-options">
        <a href="#skills" className={activeLink === 'skills' ? 'active menu-link' : 'menu-link'} onClick={() => onUpdateActiveLink('skills')}><BsFillTerminalFill size='1.3em'/></a>
        <span className="span-menu">Skills</span>
      </div>
      <div className="menu-options">
        <a href="#projects" className={activeLink === 'projects' ? 'active menu-link' : 'menu-link'} onClick={() => onUpdateActiveLink('projects')}><BsFillBriefcaseFill size='1.3em'/></a>
        <span className="span-menu">Projects</span>
      </div>
      <div className="menu-options">
        <a href="#contact" className={activeLink === 'contact' ? 'active menu-link' : 'menu-link'} onClick={() => onUpdateActiveLink('contact')}><BsFillChatSquareTextFill size='1.3em'/></a>
        <span className="span-menu">Contact</span>
      </div>
      <div className="menu-options">
        <a href="#config" className={activeLink === 'config' ? 'active menu-link' : 'menu-link'} onClick={() => onUpdateActiveLink('config')}><BsFillGearFill size='1.3em'/></a>
        <span className="span-menu span-config">
          Config.
          <div className='span-lang'>            
            <div className='langs'>
              <a href="/">Spanish</a>
              <a href="/">English</a>
            </div>
            <div className='lang-title'>Languages</div> 
          </div>
        </span>
      </div>
    </nav>
  )
}
