import '../hojas-de-estilo/Banner.css';
import { useState, useEffect } from 'react';
import 'animate.css';
import TrackVisibility from 'react-on-screen';

export const Banner = () => {
  const [loopNum, setLoopNum] = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  const toRotate = [ 'Full-Stack Developer', '3D Artist', 'Game Developer', 'IT Student'];
  const [text, setText] = useState('');
  const [delta, setDelta] = useState(125 - Math.random() * 100);
  const period = 2000;

  useEffect(() => {
    let ticker = setInterval(() => {
      tick();
    },delta)

    return () => { clearInterval(ticker) };
  }, [text])

  const tick = () => {
    let i = loopNum % toRotate.length;
    let fullText = toRotate[i];
    let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);

    setText(updatedText);

    if (isDeleting) {
      setDelta(prevDelta => prevDelta /2)
    }

    if (!isDeleting && updatedText === fullText) {
      setIsDeleting(true);
      setDelta(period);
    } else if(isDeleting && updatedText === '') {
      setIsDeleting(false);
      setLoopNum(loopNum + 1);
      setDelta(125);
    }
  }

  return (
    <section className="banner" id="home">
      
      <TrackVisibility>
      {({ isVisible }) => 
      <div className={isVisible ? 'animate__animated animate__fadeIn' : ''}>
        <div className='text-banner'>
          <h1 className="title-banner">Hi, I'm Nicolas</h1>
          <p className="wrap">I'm a <span className="wrap">{text}</span></p>
        </div>
      </div>}
      </TrackVisibility>

      <TrackVisibility>
      {({ isVisible }) => 
      <div className={isVisible ? 'animate__animated animate__fadeInDown' : ''}>
          <div className='flecha-banner'>
            <a href="#about"><div className="scroll-down">
                <div class="arrow">
                  <span></span>
                  <span></span>
                  <span></span>
                </div>
            </div></a>
          </div>
      </div>}
      </TrackVisibility>
      <div id='estrellas'>
        <div id='stars'></div>
        <div id='stars2'></div>
      </div>
    </section>
  )
}